<template lang="html">
  <div class="meeting">


    <b-card  bg-variant="white" text-variant="dark" v-show="true">
        <b-row>
          <b-col sm="8">
            <div class="" align="left">

              <h5>{{ meeting.name}} <b-badge variant="secondary"> : {{meeting.numberOfParticipants}}/{{ meeting.maxParticipants }} </b-badge> </h5>

              <p>
                  <b-badge variant="danger">
                    : {{ category[meeting.categoryId - 1].text }}
                  </b-badge>
                  <b-badge variant="warning">
                    <!-- <b-form-input size="sm" v-model="meeting.startTime"
                            type="date"
                            disabled

                          ></b-form-input> -->
                : {{ meeting.startTime }}
                </b-badge>
            </p>
          </div>
          </b-col>
          <b-col sm="4">
            <div>
              <b-button-group vertical size="sm">
                <b-button variant="outline-secondary" v-on:click="unSubscribe(index)">  </b-button>
              </b-button-group>
            </div>
          </b-col>
        </b-row>

        <b-row>
          <b-col>

<!--
              <b-btn block @click="showCollapse = !showCollapse"
                  :class="showCollapse ? 'collapsed' : null"
                  aria-controls="collapse4"
                  :aria-expanded="showCollapse ? 'true' : 'false'">
                   
              </b-btn>
              <b-collapse class="mt-2" v-model="showCollapse" id="collapse4">
              <b-card>
                <p>: {{ meeting.description }}</p>
                <p class="card-text">
                  : {{ meeting.meetingAddress.locality}}, {{meeting.meetingAddress.street}}, {{meeting.meetingAddress.house}}
                </p>
                <p>    : {{ meeting.maxParticipants }}</p>
                <p>  : {{ meeting.minAge }}</p>
              </b-card>
              </b-collapse> -->

              <b-btn block @click="showCollapseMethod()"
                  variant="secondary"
                  :class="showCollapse ? 'collapsed' : null"
                  aria-controls="collapse4"
                  :aria-expanded="showCollapse ? 'true' : 'false'">
                  <i class="fas fa-align-justify"></i>
                   
              </b-btn>

              <b-collapse class="mt-2" v-model="showCollapse" id="collapse4">
                <b-card>

                    <b-row>
                      <b-col>
                        <b-card-group deck
                            class="mb-3">

                            <b-card
                              border-variant="dark"
                              header-bg-variant=""
                              header-text-variant=""
                              bg-variant="light"
                              text-variant="dark"
                              header=":"
                              class="text-center">
                                <p class="card-text">{{ meeting.description }}</p>


                                <b-button block disabled variant="success"><i class="fas fa-clock"></i> : {{ meeting.startTime }} </b-button>
                                <b-button block disabled variant="danger"><i class="fas fa-clock"></i> : {{ meeting.endTime}} </b-button>

                              </b-card>

                        </b-card-group>
                      </b-col>

                      <b-col>
                        <b-card-header
                          border-variant="dark"
                          header-bg-variant="secondary"
                          header-text-variant="white">
                          <i class="fas fa-location-arrow"></i>  {{ meeting.meetingAddress.locality}}, {{meeting.meetingAddress.street}}, {{meeting.meetingAddress.house}}
                        </b-card-header>
                        <b-card
                          border-variant="white"
                          bg-variant="light"
                          text-variant="dark"
                          class="text-center">
                          <!--   -->
                          <gmap-map
                            :center="center"
                            :zoom="14"
                            style="width:100%;  height: 200px"
                            >

                            <gmap-marker
                              :position="marker"
                              @click="center=marker">
                            </gmap-marker>
                          </gmap-map>
                        </b-card>
                      </b-col>
                    </b-row>

                <!--    -->
                <div class="participantsInfo">
                    <b-btn block @click="showCollapseParticipantsMethod()"
                        variant="outline-dark"
                        :class="showCollapseParticipants ? 'collapsed' : null"
                        aria-controls="collapse5"
                        :aria-expanded="showCollapseParticipants ? 'true' : 'false'">
                        <i class="fas fa-users"></i>
                           
                    </b-btn>
                </div>
                <b-collapse class="mt-2" v-model="showCollapseParticipants" id="collapse5">
                    <b-table striped hover :items="items" :fields="fields">b-button</b-table>
                    <strong>*</strong>       ,    !
                </b-collapse>
              </b-card>
            </b-collapse>

          </b-col>
        </b-row>
      </b-card>

  </div>

</template>

<script>
import axios from 'axios'
export default {
  props: ['meeting', 'index'],
  data() {
    return {
      center: {lat: 0, lng: 0},
      marker: {lat: 0, lng: 0},
      showCollapse: false,
      showEditingMeeting: false,
      showCollapseParticipants: false,
      category: [
        { value: '1', text: '   ' },
        { value: '2', text: '  ' },
        { value: '3', text: '' },
        { value: '4', text: '  '},
        { value: '5', text: '  '},
        { value: '6', text: ''},
        { value: '7', text: ''},
        { value: '8', text: '  '},
        { value: '9', text: ''},
        { value: '10', text: ''},
        { value: '11', text: ' '},
        { value: '12', text: ''},
        { value: '13', text: ''},
        { value: '14', text: ''},
        { value: '15', text: ''},
        { value: '16', text: ''},
        { value: '17', text: ''},
        { value: '18', text: ''},
        { value: '19', text: ''},
      ],

      fields: [
        {
          key: 'username',
          label: ' ',
          sortable: true
        },
        {
          key: 'firstName',
          label: '',
          sortable: true
        },
        {
          key: 'lastName',
          label: '',
          sortable: true
        },
        {
          key: 'userContact.preferred',
          label: ' ',
          sortable: false
        },
        {
          key: 'userAddress.locality',
          label: '',
          sortable: true
        }

      ],

      items: [
        {
          "id": 101,
          "username": "mglmdltdxl",
          "firstName": "",
          "lastName": "",
          "sex": "m",
          "birthday": "1983-08-22",
          "userAddress": {
              "id": 101,
              "locality": "-",
              "street": "",
              "house": "47",
              "latitude": 33,
              "longitude": 21
        },
        "userContact": {
            "id": 101,
            "email": "llfuroowik@gmail.com",
            "phoneNumber": "38855205",
            "vkReference": "uagjezta",
            "facebookReference": "vgyvihjaz",
            "twitterReference": "vmjjfuujvw",
            "preferred": "e"
        }
    },
    {
        "id": 105,
        "username": "zcleusdess",
        "firstName": "",
        "lastName": "",
        "sex": "m",
        "birthday": "1999-06-12",
        "userAddress": {
            "id": 120,
            "locality": "",
            "street": "",
            "house": "4",
            "latitude": 29,
            "longitude": 13
        },
        "userContact": {
            "id": 105,
            "email": "wntinowcqx@mail.ru",
            "phoneNumber": "32699223",
            "vkReference": "fasxayce",
            "facebookReference": "hgmjehrgg",
            "twitterReference": "goxvdwencj",
            "preferred": "f"
        }
    },
    {
        "id": 111,
        "username": "yfkyflftqf",
        "firstName": "",
        "lastName": "",
        "sex": "m",
        "birthday": "1975-12-21",
        "userAddress": {
            "id": 186,
            "locality": "",
            "street": "",
            "house": "71",
            "latitude": 37,
            "longitude": 20
        },
        "userContact": {
            "id": 111,
            "email": "mpkpfpfovr@yandex.ru",
            "phoneNumber": "29621232",
            "vkReference": "ybmeyzuh",
            "facebookReference": "zieocfhhx",
            "twitterReference": "znrcbvuqtc",
            "preferred": "v"
        }
    }
],



    }
  },

  methods: {
    unSubscribe: function(index){
      this.$emit('unSubscribe', index);
    },

    showCollapseMethod() {
      this.showCollapse = !this.showCollapse;
      this.center.lat = this.meeting.meetingAddress.latitude;
      this.center.lng = this.meeting.meetingAddress.longitude;
      this.marker.lat = this.meeting.meetingAddress.latitude;
      this.marker.lng = this.meeting.meetingAddress.longitude;
    },

    showCollapseParticipantsMethod() {
      this.showCollapseParticipants = !this.showCollapseParticipants;
      if (this.showCollapseParticipants) {
        axios.get("/meeting/participants?id="+this.meeting.id)
        .then(response => {
          if (response.status === 200) {
            this.items= response.data;
            for (var i = 0; i < this.items.length; i++) {
              switch (this.items[i].userContact.preferred) {
                case "e":
                    this.items[i].userContact.preferred = "email: " + this.items[i].userContact.email;
                    break;
                case "v":
                    this.items[i].userContact.preferred = "VK: " + this.items[i].userContact.vkReference;
                    break;
                case "f":
                    this.items[i].userContact.preferred = "Facebook: " + this.items[i].userContact.facebookReference;
                    break;
                case "t":
                    this.items[i].userContact.preferred = "Twitter: " + this.items[i].userContact.twitterReference;
                    break;
                case "p":
                    this.items[i].userContact.preferred = ": " + this.items[i].userContact.phoneNumber;
                    break;
                default: this.items[i].userContact.preferred = ""

              }
            }
          }
        }).catch(function (error) {
          alert("Error /meeting/participants?Id");
          console.log(error);
        });
      }
    }
  }
}
</script>

<style lang="css">
</style>



